@import "../../../../scss/index.scss";

.navigation-footer {
  h3 {
    margin-left: 10px;
    color: $font-light;
    font-weight: bold;
  }

  ul {
    padding: 0;

    li {
      list-style: none;
      padding: 5px 0;

      a {
        color: $primary-color-dark;
        &:hover {
          color: $primary-color;
        }
      }

      span {
        padding-right: 5px;
      }
    }
  }
}
