// Variables SASS

// Default
$default-font-size: 16px;

// Colors
$primary-color: #4151ba;
$primary-color-hover: #0280b3;
$primary-font-text: 100;
$primary-color-light: #64c1e4;
$primary-color-dark: #8698a5;

// Menu
$menu-color: #252527;

// Fonts
$font-light: #fff;
$font-grey-light: #e8e8e8;
$font-grey: #808080;
$font-grey-dark: #000;
$font-dark-light: #252527;
$font-dark: #000;

// Boders
$border-light: #fff;
$border-grey-light: #e8e8e8;
$border-grey: #808080;
$border-grey-dark: #595959;
$border-dark-light: #252527;
$border-dark: #000;

// Background
$background-light: #fff;
$background-grey-light: #e8e8e8;
$background-grey: #808080;
$background-grey-dark: #595959;
$background-dark-light: #252527;
$background-dark: #000;
$background-error: #ff00001f;
$background-success: #84b84c1f;
$background-dark-mode: #16202b;
$background-dark-light-web: #1a2734;

$success: #84b84c;
$error: #f00;
$warning: orange;

// Social Color
$youtube: #cd201f;
$twitter: #1da1f2;
$facebook: #3b5998;
$linkedin: #0077b5;

// Responsive down size
$media-breakpoint-down-xs: 413.98px;
$media-breakpoint-down-xm: 573.98px;
$media-breakpoint-down-md: 767.98px;
$media-breakpoint-down-lg: 991.98px;
$media-breakpoint-down-xl: 1199.98px;

// Responsive up size
$media-breakpoint-up-xs: 320px;
$media-breakpoint-up-sm: 576px;
$media-breakpoint-up-md: 768px;
$media-breakpoint-up-lg: 992px;
$media-breakpoint-up-xl: 1200px;

$footer-color: rgba(35, 162, 226, 0.503);
